/* Custom styling */

.popUp_loader__ji3PT {
    border: 6px solid #f3f3f3; /* Light grey */
    margin: auto;
    border-top: 6px solid #3498db; /* Blue */
    border-radius: 50%;
    width: 30px;
    height: 30px;
    -webkit-animation: popUp_spin__1nLou 2s linear infinite;
            animation: popUp_spin__1nLou 2s linear infinite;
    }

@-webkit-keyframes popUp_spin__1nLou {
0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes popUp_spin__1nLou {
0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

/* Style The Dropdown Button */
.popUp_dropbtn__1fyGN {
  background-color: #4CAF50;
  color: white;
  padding: 16px;
  font-size: 16px;
  border: none;
  cursor: pointer;
}

/* The container <div> - needed to position the dropdown content */
.popUp_dropdown__2vgTr {
  position: relative;
  display: inline-block;
}

/* Dropdown Content (Hidden by Default) */
.popUp_dropdown_content__1rTux {
  display: block;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
}

/* Links inside the dropdown */
.popUp_dropdown_content__1rTux a {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}

